<template>
	<div>
		<div v-show="UserPlan == 0">
			<b-card text-variant="center" class="card allhover" @click="GotoShop">
				<b-avatar variant="primary" size="70" class="shadow mb-2">
					<feather-icon size="28" icon="ShoppingCartIcon" />
				</b-avatar>
				<h4 class="mb-1 mt-50">
					
				</h4>
				<b-card-text class="m-auto w-75"><strong>24</strong>1G </b-card-text>
			</b-card>
			<b-row class="match-height">
				<!-- <b-col lg="6" md="6">
					<notice-card />
				</b-col> -->
				<b-col lg="6" md="12">
					<node-cover />
				</b-col>
			</b-row>
		</div>
		<div v-show="show" style="height: 70vh;line-height: 70vh;" align="center">
			<b-spinner type="grow" label="Loading..." />
		</div>
		<div v-show="UserPlan != 0 && !show">
			<b-row>
				<b-col lg="3" v-for="cards in BannerData" :key="cards.icon" v-b-tooltip.hover.v-danger :title="cards.infos" @click="RestartData(cards.info)">
					<b-card class="text-center allhover">
						<div class="truncate">
							<h2 class="mb-25 font-weight-bolder" v-if="cards.icon == 'ClockIcon' && cards.title != ''">
								<!-- {{ cards.icon == 'ClockIcon' && cards.title != '' ? : cards.title }} -->
								{{ cards.title | formatDate }}
							</h2>
							<h2 class="mb-25 font-weight-bolder" v-else>
								<!-- {{ cards.icon == 'ClockIcon' && cards.title != '' ? : cards.title }} -->
								{{ cards.title }}
							</h2>
							<span>{{ cards.info }}</span>
						</div>
					</b-card>
				</b-col>
			</b-row>
			<b-card class="NodeListBody">
				<div class="CardHeader">
					<h4></h4>
					<div>
						<b-button variant="flat-primary" @click="copyLink(userSub.subscribe_url)">
							<feather-icon icon="CopyIcon" class="mr-50" />
							<span class="align-middle"></span>
						</b-button>
						<b-button
							@click="showMsgBoxTwo()"
							v-b-tooltip.hover.v-danger
							title=""
							variant="flat-primary"
						>
							<feather-icon icon="RefreshCwIcon" class="mr-50" />
							<span class="align-middle"></span>
						</b-button>
					</div>
				</div>
				<div class="mt-2">
					<b-tabs justified>
						<b-tab v-for="tab in TableData" :key="tab.name" :title="tab.name" @click="SeleTab = tab.type"> </b-tab>
					</b-tabs>
				</div>
			</b-card>
			<div class="NodeListCard">
				<b-card
					text-variant="center"
					class=" col-md-2 col-sm-12 card allhover"
					v-for="item in NodeList"
					:key="item.port"
					v-show="item.type === SeleTab"
				>
					<b-avatar variant="primary" size="40" class="shadow mb-2">
						<b-img :src="require('@/assets/images/flags/' + item.name.substr(0, 2).toLowerCase() + '.svg')" />
					</b-avatar>
					<b-card-text class="m-auto w-75">{{ item.name }}</b-card-text>
				</b-card>
			</div>
		</div>
	</div>
</template>

<script>
import {
	BCard,
	BCardText,
	BLink,
	BImg,
	BAvatar,
	VBTooltip,
	BButton,
	BRow,
	BCol,
	BCardHeader,
	BCardTitle,
	BCardBody,
	BMedia,
	BMediaAside,
	BMediaBody,
	BTabs,
	BTab,
	BSpinner
} from 'bootstrap-vue'
import { formatDate } from '@/libs/date'
import NoticeCard from '@/components/notice.vue'
import NodeCover from '@/components/nodecover.vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
	components: {
		BRow,
		BCol,
		BCard,
		BCardText,
		BLink,
		BAvatar,
		BImg,
		BCardHeader,
		BCardTitle,
		BCardBody,
		BMedia,
		BButton,
		BMediaAside,
		BAvatar,
		BMediaBody,
		BTabs,
		BTab,
		NoticeCard,
		NodeCover,
		BSpinner
	},
	directives: {
		'b-tooltip': VBTooltip
	},
	filters: {
		formatDate(time) {
			time = time * 1000
			var date = new Date(time)
			return formatDate(date, 'yyyy-MM-dd')
		}
	},
	data() {
		return {
			UserPlan: 1,
			BannerData: [
				{ info: '', title: 'Null', icon: 'CloudSnowIcon', infos: '' },
				{ info: '', title: 'Null', icon: 'CloudRainIcon', infos: '' },
				{ info: '', title: 'Null', icon: 'ClockIcon', infos: '' },
				{ info: '', title: 'Null', icon: 'CheckSquareIcon', infos: '' }
			],
			TableData: [
				{ name: 'Shadowsocks', type: 'shadowsocks' },
				{ name: 'V2ray', type: 'v2ray' }
			],
			SeleTab: 'shadowsocks',
			NodeList: [],
			userInfo: {},
			userSub: { plan: { name: 'Loading' }, expired_at: null },
			walletAmount: 'Loading',
			commissionAmount: 'Loading',
			subLeft: 'Loading',
			stat: [0, 0, 0],
			nodes: {},
			traffic_stat: 'Loading',
			gettingSub: true,
			usedPercent: '0%',
			notice: [],
			show: true
		}
	},
	mounted() {
		//this.GetNotice()
		console.log(this.$cookies.get('isLogin'))
		if (!this.$cookies.get('isLogin')) {
			this.$router.push({
				name: 'Login'
			})
		}
		//console.log(this.$store.state.userconfig.isLogin, this.$store.state.userconfig.userSub)
		this.Init()
	},
	methods: {
		InitData(res) {
			let now = Math.ceil(new Date().getTime() / 1000)
			let days = Math.ceil((res.expired_at - now) / (60 * 60 * 24))
			this.BannerData[0].title = res.plan.name
			this.BannerData[1].title = this.bit2DataUnit(parseInt(res.d) + parseInt(res.u)) + ' / ' + this.bit2DataUnit(res.transfer_enable)
			this.BannerData[2].title = res.expired_at ? res.expired_at : ''
			this.BannerData[3] = {
				info: '',
				title: res.reset_day != null ? res.reset_day + '' : '',
				icon: 'CheckSquareIcon',
				infos: res.reset_day != null ? '' + res.reset_day + '' : ''
			}
		},
		Init() {
			this.$http({
				url: '/api/v1/user/getSubscribe',
				method: 'GET'
			})
				.then(res => {
					this.gettingSub = false
					res = res.data.data
					if (res) {
						this.$store.commit('setUserSub', res)
						this.userSub = res
						this.show = false
						// if (this.$store.state.userSub.plan_id == null) {
						// 	this.$router.push({ path: '/plan' })
						// }
						if (res.plan_id != null) {
							this.getNode()
							this.InitData(res)
						} else {
							this.UserPlan = 0
						}
					}
				})
				.catch(err => {
					if (err.response && err.response.data) {
						this.$cookies.set('isLogin', 0)
						this.$router.push({
							name: 'Login'
						})
					}
					this.gettingSub = false
				})

			// this.$http({
			// 	url: '/api/v1/user/getStat',
			// 	method: 'GET'
			// })
			// 	.then(res => {
			// 		res = res.data.data
			// 		if (res) {
			// 			// this.nodes = res;
			// 		}
			// 	})
			// 	.catch(err => {
			// 		if (err.response.data.message) {
			// 			this.$cookies.set('isLogin', 0)
			// 			this.$router.push({
			// 				name: 'Login'
			// 			})
			// 		}
			// 	})
		},
		getNode() {
			this.$http({
				url: '/api/v1/user/server/fetch',
				method: 'GET'
			})
				.then(res => {
					res = res.data.data
					if (res) {
						let newArray = []
						this.NodeList = res
					}
				})
				.catch(err => {
					if (err.response.data.message) {
						this.$cookies.set('isLogin', 0)
						this.$router.push({
							name: 'Login'
						})
					}
				})
		},
		showToast(variant, position, title, text) {
			this.$toast(
				{
					component: ToastificationContent,
					props: {
						title: title,
						icon: 'InfoIcon',
						text: text,
						variant
					}
				},
				{
					position
				}
			)
		},
		GotoShop() {
			this.$router.push('/shop')
		},
		importToClash(link) {
			let fLink = 'clash://install-config?url=' + link
			window.location.href = fLink
		},
		copyLink(link) {
			var aux = document.createElement('input')
			aux.setAttribute('value', link)
			document.body.appendChild(aux)
			aux.select()
			document.execCommand('copy')
			document.body.removeChild(aux)
			this.showToast('success', 'top-center', '', 'URl')
		},
		resetUUID() {
			this.$http({
				url: '/api/v1/user/resetSecurity',
				method: 'GET'
			})
				.then(res => {
					res = res.data.data
					if (res) {
						this.showToast('success', 'top-center', '', '')
						this.userSub.subscribe_url = res
					}
				})
				.catch(err => {
					if (err.response.data.message) {
						this.showToast('danger', 'top-center', '', err.response.data.message)
					}
				})
		},
		GetNotice() {
			this.$http({
				url: '/api/v1/user/notice/fetch',
				method: 'GET'
			})
				.then(res => {
					res = res.data
					if (res) {
						this.notice = res.data
					}
				})
				.catch(err => {
					if (err.response.data.message) {
					}
				})
		},
		RestartData(info) {
			switch (info) {
				case '':
					return this.RestartData1()
				case '':
					return this.shoprenew()
			}
		},
		RestartData1() {
			if (this.userSub.plan.onetime_price || this.userSub.plan.reset_price == null) {
				this.showToast('danger', 'top-center', '', '')
			} else {
				this.$bvModal
					.msgBoxConfirm('' + this.userSub.plan.reset_price / 100 + '', {
						title: '',
						size: 'sm',
						okVariant: 'primary',
						okTitle: '',
						cancelTitle: '',
						cancelVariant: 'outline-secondary',
						hideHeaderClose: false,
						centered: true
					})
					.then(value => {
						if (value) {
							let data = {
								plan_id: this.userSub.plan.id,
								cycle: 'reset_price'
							}
							this.$http({
								url: '/api/v1/user/order/save',
								method: 'POST',
								data: data
							})
								.then(res => {
									res = res.data.data
									if (res) {
										this.$router.push({
											name: 'payorder',
											params: { id: res }
										})
									}
								})
								.catch(err => {
									if (err.response) {
										this.showToast('danger', 'top-center', '', err.response.data.message)
									}
								})
						}
					})
			}
		},
		shoprenew(id) {
			this.$bvModal
				.msgBoxConfirm('', {
					title: '',
					size: 'sm',
					okVariant: 'primary',
					okTitle: '',
					cancelTitle: '',
					cancelVariant: 'outline-secondary',
					hideHeaderClose: false,
					centered: true
				})
				.then(value => {
					//this.boxTwo = value
					if (value) {
						this.$router.push({ name: 'shoprenew', params: { id: this.userSub.plan.id } })
					}
				})
		},
		showMsgBoxTwo() {
			//this.boxTwo = ''
			this.$bvModal
				.msgBoxConfirm('', {
					title: '',
					size: 'sm',
					okVariant: 'primary',
					okTitle: '',
					cancelTitle: '',
					cancelVariant: 'outline-secondary',
					hideHeaderClose: false,
					centered: true
				})
				.then(value => {
					//this.boxTwo = value
					if (value) {
						this.resetUUID()
					}
				})
		},
		renew() {
			this.$router.push({ path: '/plan/' + this.userSub.plan_id })
		},
		bit2DataUnit(bit) {
			if (bit > 1024 * 1024 * 1024) {
				return (bit / 1024 / 1024 / 1024).toFixed(2) + ' GB'
			} else if (bit > 1024 * 1024) {
				return (bit / 1024 / 1024).toFixed(2) + ' MB'
			} else {
				return (bit / 1024 / 1024).toFixed(2) + ' KB'
			}
		},
		getUsedPercent(d, u, trans_enable) {
			return ((parseInt(d) + parseInt(u)) / parseInt(trans_enable)) * 100 + '%'
		}
	}
}
</script>

<style lang="scss">
.CardHeader {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.NodeListBody {
	.card-body {
		padding-bottom: 0 !important;
	}
}
.NodeListCard {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	img {
		width: 100%;
	}
	.card.col-md-2 {
		min-width: 23% !important;
		margin-right: 2%;
	}
}
</style>
